let temConfig = null;
// axios.defaults.baseURL = process.env.NODE_ENV === 'development' ?  '' : 'http://jgapi.china-mail.com.cn';
//请求拦截
axios.interceptors.request.use(
    config => {
        let token = process.env.NODE_ENV === 'development' ? 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJcIjc1RUQ4OUMzNDQ5MDRCODc4RUVCRkI5QUU5QzgyODBDXCIiLCJleHAiOjE2MTgwMzMxMjV9.63yCdvvumIRzZ-GqYz8j90MHCzXwc7dNAiJDzP-8t38' : sessionStorage.getItem("token");
        config.headers.Authorization = `${token}`;
        if (config.method === "post" && config.headers['Content-Type'] != 'multipart/form-data') {
            // config.data = qs.stringify(config.data);
            console.log(config.data);
        }
        return config;
    }
)
//响应拦截
axios.interceptors.response.use(
    res => {
        return res.data;
    },
    error => {
        let errMsg = '网路错误';
        switch (error.response.status) {
            case 400:
                errMsg = '网路错误';
                break;
            case 403:
                errMsg = '拒绝访问';
                break;
            case 404:
                errMsg = '请求地址未找到';
                break;
            case 408:
                errMsg = '请求超时';
                break;
            case 500:
                errMsg = '服务器内部错误';
                break;
            case 501:
                errMsg = '服务未实现';
                break;
            case 502:
                errMsg = '网关错误';
                break;
            case 503:
                errMsg = '服务不可用';
                break;
            case 504:
                errMsg = '网关超时';
                break;
            case 505:
                errMsg = 'HTTP版本不受支持';
                break;
        }
        Toast.fail(errMsg);
        return Promise.reject(error);
    }
)
export default axios;